.card-container {
    position: static;
    max-width: 60rem;
    min-height: 42rem;
    max-height: 52rem;
    background-color: #fff;
    border-radius: 2rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.image-container {
    position: static;
    max-width: 55rem;
    min-height: 30rem;
    max-height: 52rem;

}

.card-image {
    position: static;
    max-width: 55rem;
    min-height: 30rem;
    max-height: 52rem;
    border-radius: 2rem;
    margin: 2rem 0rem 0rem 0rem ;
}

.text-container {
    color: #000;
    margin: -2rem 0rem 0rem 0rem ;
}

.card-title {
    font-size: 1.2rem;
    max-width: 40rem;
    text-align: center;
}

.card-text {
    max-width: 40rem;
    margin: 0.5rem 0rem 0rem 0rem ;
    text-align: center;
}








