.main-container {
    position: static;
    /* max-width: 100vw; */
    min-height: 70vh;
    background-image: url('../../public/images/Email.jpg');
    display: flex;
    flex-direction: column;
}

.title {
    position: static;
    max-width: 40rem;
    margin: 3.3rem 0rem 0rem 10rem ;
    color: #fff;
    font-size: 3.3rem;
}

.title-span {
    text-decoration: none;
    color: rgb(83, 43, 226);
}

.icon-user {
    position: static;
    max-width: 40rem;
    min-height: 3.5rem;
    margin: 3rem 0rem 0rem 10rem ;
    color: #fff;
}

.icon-user > i:hover {
    color: rgb(83, 43, 226);
    transition: all 0.3s ease-in-out;
}

.icon-shield {
    position: static;
    max-width: 40rem;
    min-height: 3.5rem;
    margin: 3rem 0rem 0rem 10rem ;
    color: #fff;

}

.icon-shield > i:hover {
    color: rgb(83, 43, 226);
    transition: all 0.3s ease-in-out;
}

.user-text {
    position: static;
    max-width: 35rem;
    min-height: 2rem;
    color: #fff;
    font-size: 1.3rem;
    margin: -3.4rem 0rem 0rem 4.5rem ;
}

.shield-text {
    position: static;
    max-width: 35rem;
    min-height: 2rem;
    color: #fff;
    font-size: 1.3rem;
    margin: -3.4rem 0rem 0rem 4.5rem ;
}