.features-container {
    position: static;
    /* max-width: 100%; */
    min-height: 120vh;
    color: #fff;
    background-image: radial-gradient(circle, #18171a, #1b1823, #1c182c, #1c1935, #1b1a3f, #1b193e, #1b183d, #1b173c, #1b1430, #191225, #160f1b, #0f0c11);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.features-text {
    position: static;
    text-align: center;
}

.features-title {
    font-size: 2.5rem;
    margin: 4.5rem 0rem 0rem 0rem ;
}

.carousel-container {
    margin: 4.5rem 0rem 0rem 0rem ;
    /* opacity: 0;
    transition-duration: 1s ease ; */
}
