.nav-container {
    position: sticky;
    top: 0;
    /* max-width: 100vw; */
}

nav {
    /* max-width: 100vw; */
    min-height: 2rem;
    background-color: #fff;
    display: flex;
}

.logo-container {
    position: static;
    margin: 2.5rem 0rem 0rem 4rem ;
}

.anchor-pox {
    color: #000;
    text-decoration: none;
}

.anchor-me {
    color: rgb(83, 43, 226);
    text-decoration: none;
}

.search-container {
    margin: 2rem 0rem 0rem 45rem ;
}

.menu-container {
    margin: 1rem 0rem 0rem 0rem ;
}

.menu-container > ul {
    list-style-type: none;
    display: flex;   
}

.menu-container > ul > li {
    margin: 1rem;
}

.btn {
    background-color: transparent;
    border-width: 0;
    border-radius: 0.6rem;
    color: #000;
}

.btn:hover {
    background-color: rgb(83, 43, 226);
    border-radius: 0.6rem;
    color: #000;
    transition: all 0.3s ease-in-out;
}